* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.search {
  background-color: #010014;
  padding: 10px;
  text-align: center;
}

.search h2 {
  color: white;
  padding: 20px;
}

.search input[type="text"] {
  width: 80%;
  height: 40px;
  padding-left: 10px;
}

.search input[type="submit"] {
  width: 80%;
  height: 40px;
  cursor: pointer;
  background-color: #0084cf;
  color: white;
  margin: 10px 0;
  border: 0;
  font-weight: bold;
}

.containerCity {
  text-align: center;
}

.containerCity p {
  border-bottom: 1px dashed #ccc;
  padding: 10px 0;
  font-weight: bold;
}

.action {
  padding: 8px;
}
